import { useState } from "react";
import { Button } from "~/components/ui/button";
import { Input } from "~/components/ui/input";
import { RadioGroup, RadioGroupItem } from "~/components/ui/radio-group";
import { Label } from "~/components/ui/label";

export type PuzzleType = "text" | "multiple_choice";

interface PuzzleChoice {
    id: string;
    text: string;
}

interface PuzzleInputProps {
    type: PuzzleType;
    choices?: PuzzleChoice[];
    onSubmit: (answer: string) => void;
    disabled?: boolean;
}

export function PuzzleInput({ type, choices = [], onSubmit, disabled }: PuzzleInputProps) {
    const [answer, setAnswer] = useState("");

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        if (!answer.trim() || disabled) return;
        onSubmit(answer.trim());
        setAnswer("");
    };

    return (
        <form onSubmit={handleSubmit} className="space-y-4">
            {type === "text" && (
                <div className="space-y-2">
                    <Input
                        type="text"
                        placeholder="Enter your answer"
                        value={answer}
                        onChange={(e) => setAnswer(e.target.value)}
                        disabled={disabled}
                        required
                    />
                </div>
            )}

            {type === "multiple_choice" && (
                <div className="space-y-4">
                    <RadioGroup
                        value={answer}
                        onValueChange={setAnswer}
                        disabled={disabled}
                        required
                    >
                        {choices.map((choice) => (
                            <div key={choice.id} className="flex items-center space-x-2">
                                <RadioGroupItem value={choice.id} id={choice.id} />
                                <Label htmlFor={choice.id}>{choice.text}</Label>
                            </div>
                        ))}
                    </RadioGroup>
                </div>
            )}

            <Button
                type="submit"
                className="w-full"
                disabled={disabled || !answer.trim()}
            >
                {disabled ? "Checking..." : "Submit Answer"}
            </Button>
        </form>
    );
} 